<template>
  <div class="marketing-set">
    <div class="marketingSet-content">
      <EcRouterBack  routerName='marketing'  />
      <h4>广告投放</h4>
      <div class="marketingSet-form">
        <div class="form-views">
          <span class="form-name">期望投放范围</span>
          <div class="form-value">
            <el-select v-model="province" class="city" @change="provinceChange(province,'province')"  placeholder="请选择">
              <el-option
                v-for="(item,index) in provinceList"
                    :key="`province_${index}`"
                    :label="item.name"
                    :value="item.id" 
                >
              </el-option>
            </el-select>
            <span class="_tip">省</span>
            <el-select v-model="city" class="city" @change="provinceChange(city,'city')"  placeholder="请选择">
              <el-option
               v-for="(item,index) in cityList"
                    :key="`city_${index}`"
                        :label="item.name"
                        :value="item.id"
                         v-show="item.id.substring(0,2)==cityId"
                      >
              </el-option>
            </el-select>
            <span class="_tip">市</span>
            <el-select v-model="country" class="city"  placeholder="请选择">
              <el-option
              v-for="(item,index) of countryList"
                        :key="`country_${index}`"
                        :label="item.name"
                        :value="item.id"
                        v-show="item.id.substring(0,4)==countryId"
                        >
              </el-option>
            </el-select>
            <span class="_tip">区</span>
          </div>
        </div>
        <div class="form-views">
          <span class="form-name">期望投放科目</span>
          <div class="form-value">
            <el-select v-model="teachAreasDict" multiple placeholder="请选择">
              <el-option
               v-for="item in coursesData"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="form-views">
          <span class="form-name">期望投放素材</span>
          <div class="from-value sourceMaterial">
            <img
              class="imgFile"
              @click="file()"
              :src="logoImg"
              alt
            />
            <input class="inputFile" ref="inputFile" @change="imgFile()" type="file" />
            <p>素材大小不得超过10MB</p>
            <p>不得包含违法内容...</p>
          </div>
        </div>
        <div class="form-views">
          <span class="form-name">期望科目报价</span>
          <div class="form-value">
            <el-input class="subject" type="number" placeholder clearable v-model="price"></el-input>
            <span class="_tip _phone">联系方式</span>
            <el-input class="subject"  type="number" @blur="phoneBoll=true"  placeholder clearable v-model="phone"></el-input>
            <span class="warning" v-if="phoneBoll==false">输入11位号码</span>
          </div>
        </div>
      </div>
      <div class="buts" @click="save_advertising()">提交</div>
    </div>
  </div>
</template>
<script>
import { province_list,city_list, country_list } from '@/utils/area'
import { advertising,queryCourses } from '@/api/marketing.js';
import { uploadSingle } from '@/api/user.js';
import Utils from '@/utils/deep.js';
import EcRouterBack from "@/components/EduactionRouterBack.vue"
export default {
  data() {
    return {
      address: '',
      logoImg:"../../../static/img/marketing/button_sczp.png",
      coursesData:[],

      country: '', // true   区/县
      city: '', // true  市
      id: '', // false  广告id
      image: '', // true 投放素材'
      phone: null, // true 联系方式
      price: 0, // true  报价'
      province: '', // true  省'
      teachAreasDict: [], // true 投放范围',

      provinceList:[],
      cityList:[],
      countryList:[],
      cityId:'11',
      countryId:'1101',
      phoneBoll:true

    }
  },
  components:{
    EcRouterBack
  },
  created() {
    this.save_queryCourses();
    this.provinceList = this.entries_list(province_list);
    this.cityList = this.entries_list(city_list);
    this.countryList = this.entries_list(country_list);
  },
  methods: {
    // img类型判断I
    imgFile() {
      let dataFoles, files;
      let REG = /\\/g
      dataFoles = this.$refs.inputFile.files;
      if (dataFoles.length != 0) {
        files = dataFoles[0];
        // if (files.type !== 'image/png') {
        //   this.$message.error('图片格式必须为 .png 格式')
        // } else 
        if (files.size > 10240) {
          this.$message.error('请选择10MB以内的图片！')
        } else {
           let formData = new FormData();
          formData.append('file',files);
          uploadSingle(formData,'agency').then( res =>{
            if( res.code == 200){
              this.logoImg = Utils.getObjectURL(this.$refs.inputFile.files[0]);
              this.image = res.data.url;
            }else{
              this.$message.error('上传失败')
            }
          })
        }
       
      }
    },
    // 省/市/区 联动切换
    provinceChange(id,name){
      if(name== 'province'){
        this.cityId = id.substring(0,2);
        this.countryId = id.substring(0,2)+'01';
        this.city = '';
        this.country = '';
      }else if(name== 'city'){
        this.countryId = id.substring(0,4);
        this.country = '';
      }
    },
    // 省/市/区 转换数组
    entries_list(data){
       let arr = [];
      for (let [id, name] of Object.entries(data)) {
        arr.push({ id, name});
      }
      return arr;
    },
    save_uploadSingle(file){
      
    },
     save_queryCourses(){
      queryCourses( '1' ).then( res =>{
        // console.log( res);
        if(res.code == 200){
          this.coursesData = res.data;
        }
      })
    },
    save_advertising(){
      advertising({
        area:country_list[this.country] , // true   区
        city: city_list[this.city], // true  市
        id: '', // false  广告id
        image: this.image, // true 投放素材'
        phone: this.phone, // true 联系方式
        price: this.price, // true  报价'
        province: province_list[this.province], // true  省'
        teachAreasDict:this.teachAreasDict.length>0?this.teachAreasDict.join(','):'', // true 投放范围'
      }).then(res => {
        // console.log(res)
         if( res.code == 200){
           this.$message({
            message: '提交成功',
            type: 'success',
            onClose:()=>{this.$emit('event1', '1')}
          });
        }else{
          this.$message.error(res.message);
        }
      })
    }
  },
  watch:{
    phone(newValue,oldValue){
      const myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
      if(newValue!=null && !myreg.test(newValue)){
        this.phoneBoll = false
      }else{
        this.phoneBoll = true
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.form-views {
  min-width: 700px;
  min-height: 30px;
  margin-bottom: 20px;
  display: flex;
  align-items: flex-start;
  .form-name {
    width: 180px;
    margin-right: 20px;
    line-height: 30px;
    text-align: right;
  }
  .form-value {
    width: 600px;
    display: flex;
    ._tip {
      line-height: 30px;
      padding: 0 6px;
    }
    ._phone {
      margin-left: 50px;
    }
    .city {
      width: 110px !important;
    }
    .subject {
      width: 150px !important;
    }
    .address {
      width: 200px !important;
    }
    .warning{
      color: red;
      line-height: 34px;
      font-size: 13px;
      padding-left: 5px;
    }
  }
  .sourceMaterial {
    width: 400px;
    display: flex;
    flex-direction: column;
    // align-content:center;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    position: relative;
    img {
      height: 110px;
      margin: 0 auto;
      margin-bottom: 15px;
      cursor: pointer;
      z-index: 1;
    }
    // img:hover {
    //   content: url('../../../static/img/marketing/button_sczp2.png');
    // }
    p {
      width: 400px;
      text-align: center;
      color: #a6a6a6;
      font-size: 14px;
      line-height: 20px;
    }
    .inputFile {
      position: absolute;
      top: 0;
      left: 50%;
      height: 110px;
      width: 110px;
      z-index: 1;
      margin-left: -55px;
      background: url('../../../static/img/marketing/button_sczp.png') no-repeat;
      background-size: cover;
      opacity: 0;
    }
    // .inputFile:hover {
    //   background: url('../../../static/img/marketing/button_sczp2.png')
    //     no-repeat;
    //   background-size: cover;
    //   opacity: 0;
    // }
  }
}
</style>


